.footer {
  margin-top: 20vh;
  position: absolute;
  width: 100vw;
  left: 0; }
  .footer .typo {
    width: fit-content;
    position: absolute;
    left: 74px;
    bottom: 74px; }
    @media (max-width: 1024px) {
      .footer .typo {
        font-size: 14px;
        left: 24px;
        bottom: 24px; } }
  .footer .social.twitter {
    height: 17px;
    width: 17px;
    position: absolute;
    right: 74px;
    bottom: 74px; }
    @media (max-width: 1024px) {
      .footer .social.twitter {
        right: 24px;
        bottom: 24px; } }
    .footer .social.twitter img {
      height: 100%;
      width: auto; }
