@font-face {
  font-family: "Sansation Bold";
  src: url("/static/fonts/Sansation_Bold.ttf"); }

@font-face {
  font-family: "Sansation Regular";
  src: url("/static/fonts/Sansation_Regular.ttf"); }

@font-face {
  font-family: "Sansation Light";
  src: url("/static/fonts/Sansation_Regular.ttf"); }

body {
  overflow-x: hidden;
  width: 100vw;
  margin: 0;
  background-color: #111524; }

.content {
  width: 100%;
  padding-bottom: 10vh; }
  .content .content-wrapper {
    padding: 0 20vw; }
    @media (max-width: 1024px) {
      .content .content-wrapper {
        padding: 0 0vw !important; } }
  @media (max-width: 1024px) {
    .content {
      padding-bottom: 8vh; } }
  .content .content-wrapper {
    padding: 0 20vw; }

.typo {
  color: white;
  text-align: center;
  letter-spacing: 1.1px;
  line-height: 21px; }
  .typo.secondary {
    color: #EA4148; }

.hero-header .logo {
  font-size: 25px;
  width: fit-content;
  position: absolute;
  left: 74px;
  top: 74px;
  letter-spacing: 5px; }
  @media (max-width: 1024px) {
    .hero-header .logo {
      font-size: 20px;
      left: 33px;
      top: 33px; } }

.hero-header .hero-img {
  height: 56vh;
  width: 40vw;
  margin: auto;
  transform: translateY(-8vh); }
  @media (max-width: 1024px) {
    .hero-header .hero-img {
      height: 60vh;
      width: 140vw;
      transform: translate(-20vw, 8vh); } }
  .hero-header .hero-img img {
    width: 100%;
    height: auto; }

.hero-header .login {
  font-size: 16px;
  width: fit-content;
  position: absolute;
  right: 74px;
  top: 74px;
  display: flex; }
  .hero-header .login span {
    margin-top: 2px;
    margin-left: 17px; }
  .hero-header .login .icon {
    height: 24px;
    width: 24px; }
    .hero-header .login .icon img {
      height: 100%;
      width: auto; }
  @media (max-width: 1024px) {
    .hero-header .login {
      right: 33px;
      top: 33px; } }

.typo-regular {
  font-family: 'Sansation Regular'; }

.typo-bold {
  font-family: 'Sansation Bold'; }

.typo-light {
  font-family: 'Sansation Light'; }

.header {
  font-size: calc(32px + (61 - 32) * ((100vw - 375px) / (1920 - 375))); }

.subtitle {
  font-size: 20px; }

.copy {
  font-size: 16px; }

.subcopy {
  font-size: 12px; }

.content .header-container {
  height: 16vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .content .header-container {
      height: 13vh; } }

.btn {
  border-radius: 100px;
  border-style: solid;
  border-width: 1px;
  width: 148px;
  height: 56px;
  margin: auto;
  font-family: 'Sansation Bold';
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .btn.primary {
    color: #EA4148;
    border-color: #EA4148; }
  .btn.secondary {
    color: white;
    border-color: white; }

.team .profile-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .team .profile-container {
      height: 35vh; } }
  .team .profile-container .profile-card {
    flex: 0 1 45%;
    display: flex;
    margin-bottom: 3vh; }
    .team .profile-container .profile-card.active {
      z-index: 1; }
    @media (max-width: 1024px) {
      .team .profile-container .profile-card {
        position: absolute;
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 98vw;
        height: 35vh;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 0;
        display: none; }
        .team .profile-container .profile-card.active {
          z-index: 1 !important;
          display: block; } }
    .team .profile-container .profile-card .avatar {
      width: 6vw;
      height: 6vw;
      margin-right: 10px; }
      @media (max-width: 1024px) {
        .team .profile-container .profile-card .avatar {
          width: 30vw;
          height: 30vw;
          margin: auto;
          margin-bottom: 5vh; } }
      .team .profile-container .profile-card .avatar img {
        position: relative;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        -webkit-box-shadow: 0 0 0 3px #fff, 0 0 0 4px #999, 0 2px 5px 4px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0 0 0 3px #fff, 0 0 0 4px #999, 0 2px 5px 4px rgba(0, 0, 0, 0.2);
        box-shadow: 0 0 0 3px #fff, 0 0 0 4px #999, 0 2px 5px 4px rgba(0, 0, 0, 0.2); }
    .team .profile-container .profile-card .words .title {
      display: flex;
      margin-bottom: 1vh; }
      @media (max-width: 1024px) {
        .team .profile-container .profile-card .words .title {
          display: flex;
          justify-content: center;
          margin-bottom: 2vh; } }
      .team .profile-container .profile-card .words .title .name {
        color: white;
        font-size: 16px;
        font-family: 'Sansation Bold'; }
        .team .profile-container .profile-card .words .title .name span {
          margin-left: 5px;
          font-family: 'Sansation Regular'; }
      .team .profile-container .profile-card .words .title .social {
        margin-left: 5px; }
        .team .profile-container .profile-card .words .title .social .twitter {
          height: 17px;
          width: 17px; }
        .team .profile-container .profile-card .words .title .social .linkedin {
          height: 17px;
          width: 17px; }
    .team .profile-container .profile-card .words .desc {
      color: white;
      font-size: 12px;
      font-family: 'Sansation Regular'; }
      @media (max-width: 1024px) {
        .team .profile-container .profile-card .words .desc {
          max-width: 65vw;
          margin: auto; } }

.team .slider-controls {
  display: none; }
  @media (max-width: 1024px) {
    .team .slider-controls {
      display: block;
      display: flex;
      justify-content: center;
      transform: translateY(2vh); }
      .team .slider-controls span {
        margin: 3px;
        height: 8px;
        width: 8px;
        border-radius: 100%;
        background-color: white; }
      .team .slider-controls .active {
        background-color: #EA4148; } }

#__next-prerender-indicator {
  display: none; }
