.economics {
  background-image: url("../images/tech/tech-bg.svg");
  background-size: cover; }
  .economics .header-container {
    margin-bottom: 6vh; }
  .economics .feature-card {
    height: fit-content;
    padding: 50px;
    margin: auto auto 5vh auto;
    background-color: #262834;
    border-radius: 10px;
    display: flex;
    flex-direction: row; }
    @media (max-width: 1024px) {
      .economics .feature-card {
        flex-direction: column;
        width: 75vw;
        padding: 30px; } }
    .economics .feature-card .divider {
      border: 1px solid #DBDBDB;
      height: 210px;
      margin-right: 100px; }
      @media (max-width: 1024px) {
        .economics .feature-card .divider {
          display: none; } }
    .economics .feature-card .icon {
      margin: auto 80px auto 30px; }
      @media (max-width: 1024px) {
        .economics .feature-card .icon {
          margin: auto; } }
      .economics .feature-card .icon .eth {
        width: 115px;
        height: 177px; }
        @media (max-width: 1024px) {
          .economics .feature-card .icon .eth {
            width: 42px;
            height: 70px; } }
        .economics .feature-card .icon .eth img {
          height: 100%;
          width: auto; }
      .economics .feature-card .icon .burn {
        width: 115px;
        height: 177px; }
        @media (max-width: 1024px) {
          .economics .feature-card .icon .burn {
            width: 42px;
            height: 70px; } }
        .economics .feature-card .icon .burn img {
          height: 100%;
          width: auto; }
    .economics .feature-card .set {
      display: flex;
      flex-direction: column; }
      .economics .feature-card .set div {
        text-align: left; }
        @media (max-width: 1024px) {
          .economics .feature-card .set div {
            text-align: center; } }
      .economics .feature-card .set .stat {
        margin: 10% 0 0 0;
        font-size: 124px;
        flex-direction: row;
        font-family: 'Sansation Bold';
        color: white;
        letter-spacing: 1.1px; }
        @media (max-width: 1024px) {
          .economics .feature-card .set .stat {
            font-size: 52px;
            margin-bottom: 3vh;
            order: 1; } }
      @media (max-width: 1024px) {
        .economics .feature-card .set .desc {
          order: 2; } }
