.technology {
  background-image: url("../images/tech/tech-bg.svg");
  background-size: cover; }
  .technology .infographics {
    height: 30vh;
    width: 100%;
    position: relative;
    margin-top: 15vh; }
    @media (max-width: 1024px) {
      .technology .infographics {
        height: 78vh;
        width: 80%;
        margin: auto;
        margin-top: 5vh; } }
    .technology .infographics .fragment {
      height: fit-content;
      width: fit-content; }
    .technology .infographics .img {
      display: block;
      margin: auto; }
    .technology .infographics .divider {
      height: 0px;
      border: 0.5px dotted grey;
      width: 70%;
      margin: auto;
      transform: translateY(15vh); }
      @media (max-width: 1024px) {
        .technology .infographics .divider {
          transform: translateY(31vh) rotate(90deg);
          width: 32vh; } }
    .technology .infographics .fox {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: auto; }
      @media (max-width: 1024px) {
        .technology .infographics .fox {
          position: absolute;
          left: calc(100% - 78px);
          right: 0;
          top: calc(50% - 100px);
          margin: 0; } }
      .technology .infographics .fox .img {
        height: 50px;
        width: 52px;
        margin-bottom: 10px; }
        .technology .infographics .fox .img img {
          height: 100%;
          width: auto; }
    .technology .infographics .dapp {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto; }
      @media (max-width: 1024px) {
        .technology .infographics .dapp {
          position: absolute;
          left: 0;
          top: calc(50% - 100px);
          margin: 0; } }
      .technology .infographics .dapp .img {
        height: 23px;
        width: 86px;
        margin-bottom: 10px; }
        .technology .infographics .dapp .img img {
          height: 100%;
          width: auto; }
    .technology .infographics .pic {
      position: absolute;
      right: 0;
      top: calc(50% - 26px); }
      @media (max-width: 1024px) {
        .technology .infographics .pic {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto; } }
      .technology .infographics .pic .img {
        width: 33px;
        height: 52px;
        margin-bottom: 30px; }
        .technology .infographics .pic .img img {
          height: 100%;
          width: auto; }
    .technology .infographics .eth {
      position: absolute;
      left: 0;
      top: calc(50% - 26px); }
      @media (max-width: 1024px) {
        .technology .infographics .eth {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          margin: auto; } }
      .technology .infographics .eth .img {
        width: 33px;
        height: 52px;
        margin-bottom: 30px; }
        .technology .infographics .eth .img img {
          height: 100%;
          width: auto; }
  .technology .formulae {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 10vh; }
    @media (max-width: 1024px) {
      .technology .formulae {
        transform: translate(4vw, -6vh);
        margin-top: 0vh; } }
    .technology .formulae .component {
      height: fit-content;
      width: fit-content; }
      .technology .formulae .component .feature {
        text-align: left; }
        @media (max-width: 1024px) {
          .technology .formulae .component .feature {
            width: 40%; } }
      .technology .formulae .component .var {
        text-align: left;
        margin: 2vh 0 0 0 !important; }
        @media (max-width: 1024px) {
          .technology .formulae .component .var {
            font-size: 20px; } }
    .technology .formulae .method {
      display: flex;
      align-items: center; }
      @media (max-width: 1024px) {
        .technology .formulae .method.plus {
          transform: translateX(-4vw); } }
  .technology .write-up {
    display: flex;
    flex-direction: row;
    margin-top: 8vh; }
    @media (max-width: 1024px) {
      .technology .write-up {
        flex-direction: column;
        margin-top: 0vh; } }
    .technology .write-up .part-one {
      text-align: left;
      padding-right: 5%; }
      @media (max-width: 1024px) {
        .technology .write-up .part-one {
          text-align: center;
          padding: 0 3vw;
          margin-bottom: 5vh; } }
    .technology .write-up .part-two {
      text-align: left; }
      @media (max-width: 1024px) {
        .technology .write-up .part-two {
          padding: 0 3vw;
          text-align: center; } }
